@import "../../scss/variables.scss";

.home-inner-container {
  display: flex;
}

.title {
  color: #fff;
  overflow: hidden;
}

.title__text {
  font-size: 5rem;
  line-height: 1;
  letter-spacing: 0.1em;
  margin-bottom: 1.6rem;
  font-family: "Future";
  font-weight: normal;
  text-shadow: 0 10px 30px rgba(2, 11, 22, 0.5);
}

.title .border {
  display: inline-block;
  margin-bottom: 3.2rem;
}

.title .border span {
  display: block;
  width: 88px;
  height: 4px;
  border-radius: 5px;
  background: $accentColor;
}
.title .border span:first-child {
  margin-bottom: 1.4rem;
}
.title .border span:last-child {
  margin-left: 54px;
}

.title__lead {
  color: #fff;
  font-size: 1.6rem;
  letter-spacing: 0.1em;
  margin-top: -10px;
}

.home-image-container {
  top: 10%;
  bottom: 0;
  height: 600px;
  width: 800px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.home-img {
  max-width: 500px;
  max-height: 500px;
}
