@font-face {
  font-family: "Future";
  src: url("./Futura ICG.ttf");
}

@font-face {
  font-family: "futura_m";
  src: url("./Futura ICG.ttf") format("ttf");
}

$fontFamily: Future, Verdana, Geneva, sans-serif;
$bodyBackground: radial-gradient(
