@import "./variables.scss";
@import "./header.scss";

html,
body {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
}

body {
  font-family: $fontFamily;
  background: $bodyBackground;
  color: $bodyFontColor;
  margin: 0;
  width: 100%;
  height: 100%;
}

h2 {
  color: $accentColor;
}

ul li {
  color: #aaa;
}

a {
  color: lightblue;
  text-decoration: none;
  &:hover {
    text-decoration: underline;
  }
}

* {
  box-sizing: border-box;
}

#root {
  width: 100%;
  height: 100%;
}

.flex {
  display: flex;
}

.outer-container {
  width: 100%;
  height: 100vh;
  margin: 0 auto;
  display: flex;
  position: relative;
  background-image: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(#072142),
    color-stop(#061c37),
    color-stop(#07182b),
    color-stop(#061220),
    to(#020b16)
  );
  background-image: -webkit-linear-gradient(
    top,
    #072142,
    #061c37,
    #07182b,
    #061220,
    #020b16
  );
  background-image: -o-linear-gradient(
    top,
    #072142,
    #061c37,
    #07182b,
    #061220,
    #020b16
  );
  background-image: linear-gradient(
    to bottom,
    #072142,
    #061c37,
    #07182b,
    #061220,
    #020b16
  );
  position: fixed;
  top: 0;
  left: 0;
}

.flex-row {
  flex-direction: row;
}

.inner-container {
  margin-left: 10%;
  margin-top: 125px;
  position: relative;
  display: flex;
  width: 100%;
  &-default {
    width: 90%;
    height: 90%;
    min-width: 90%;
    min-height: 90%;
    background: rgba(0, 0, 0, 0.5);
    padding: 25px;
    overflow-y: scroll;
    max-height: 500px;
  }
}

.fade {
  transition: opacity ease-in 0.3s;
  opacity: 1;
}

.fade-out {
  opacity: 0;
}

.fade-in {
  opacity: 1;
}

.slider {
  transition: margin-top ease-in 0.3s;
  width: 100%;
}

.slide-up-start {
  position: absolute;
  margin-top: 1000px;
}

.slide-up-end {
  margin-top: 125px;
}

@media only screen and (max-width: 600px) {
  body {
    background: green;
  }
  .author {
    display: none;
  }
  .title__text {
    font-size: 60px;
  }
}
