.project-item-container {
  width: 100%;
  min-height: 200px;
  display: flex;
  margin-bottom: 50px;
}

.project-image-thumb {
  max-width: 300px;
  border: 2px solid #ccc;
  border-radius: 5px;
  margin: 5px;
  margin-right: 10px;
}

@media only screen and (max-width: 600px) {
  .project-item-container {
    display: block;
    border-top: 1px solid #555;
    padding-top: 10px;
    margin-bottom: 10px;
  }

  .project-image-thumb {
    max-width: 190px;
    border: 2px solid #ccc;
    border-radius: 5px;
    margin: 5px;
    margin-right: 10px;
  }
}
