.title-text {
  font-size: 12rem;
  line-height: 1;
  letter-spacing: 0.1em;
  margin-bottom: 1.6rem;
  font-family: "Future";
  font-weight: normal;
  text-shadow: 0 10px 30px rgba(2, 11, 22, 0.5);
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 40px 40px 0;
  color: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  z-index: 1000;
}

.menu-item {
  margin-right: 25px;
  cursor: pointer;
  color: #bbb;
}

.menu-item-active {
  color: $accentColor;
  margin-right: 25px;
  cursor: pointer;
}

.menu-item:hover {
  color: white;
}

.wrap {
  display: flex;
  position: relative;
}

.links-images {
  max-width: 25px;
  stroke: white;
}

.menuIcon {
  position: relative;
  width: 50px;
  height: 22px;
  cursor: pointer;
  margin-left: 50px;
}
@media screen and (max-width: 420px) {
  .menuIcon {
    width: 25px;
    height: 15px;
  }
}
.menuIcon:after,
.menuIcon:before {
  content: "";
  width: 100%;
  height: 3px;
  background: #fff;
  position: absolute;
  right: 0;
  -webkit-transition: 0.4s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  -o-transition: 0.4s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  transition: 0.4s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}
.menuIcon:after {
  top: 0;
}
.menuIcon:before {
  bottom: 0;
}
.menuIcon.js-menuOpen:after {
  top: 50%;
  -webkit-transform: rotate(45deg) translate3d(0, -50%, 0);
  transform: rotate(45deg) translate3d(0, -50%, 0);
}
.menuIcon.js-menuOpen:before {
  bottom: 50%;
  -webkit-transform: rotate(-45deg) translate3d(0, 50%, 0);
  transform: rotate(-45deg) translate3d(0, 50%, 0);
}
